body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  background: -webkit-gradient(linear, left top, right bottom, color-stop(-100%, #000000), to(#ffffff));
  background: -webkit-linear-gradient(top left, #000000 -100%, #ffffff 100%);
  background: -o-linear-gradient(top left, #000000 -100%, #ffffff 100%);
  background: linear-gradient(to bottom right, #000000 -100%, #ffffff 100%);
}




